export default {
	data() {
		return {
			user:'',
			isok:false,
			text:'点击登录/注册'
		}
	},
	methods: {
		/* 登录/注册 */
		toLogin(){
			uni.navigateTo({
			    url: '../login/login'
			});
		},
		/* 个人信息 */
		tousermsg(){
			uni.navigateTo({
			    url: '../../component/usermsg/usermsg'
			});
		},
		/* 订单列表 */
		touserorder(){
			uni.navigateTo({
			    url: '../../component/userorder/userorder'
			});
		},
		tousernotice(){
			uni.navigateTo({
			    url: '../../component/usernotice/usernotice'
			});
		}
		
	},
	created() {
		let self=this
		uni.getStorage({
			key:'user_phone',
			success: function (res) {
			    console.log(res.data)
				if(res.data){
					self.user=res.data
					self.isok=false
				}
			}
		})
		
	}
}
